import * as THREE from 'three'

import Debug from './Utils/Debug.js'
import Sizes from './Utils/Sizes.js'
import Time from './Utils/Time.js'
import Camera from './Camera.js'
import Renderer from './Renderer.js'
import World from './World/World.js'
import Resources from './Utils/Resources.js'



// utils & constant
import SCENES from './Utils/SCENES.js'

import sources from './sources.js'
import Composer from "./Composer";

let instance = null

export default class Experience {
    composerEnable = false;

    constructor(_canvas) {
        // Singleton
        if (instance) {
            return instance
        }
        instance = this

        // Global access
        window.experience = this

        // Options
        this.canvas = _canvas

        // Setup
        this.debug = new Debug()
        this.sizes = new Sizes()
        this.time = new Time()
        this.scene = new THREE.Scene()
        this.resources = new Resources(sources)
        this.camera = new Camera()
        this.renderer = new Renderer()
        this.composer = new Composer()
        this.world = new World()

        const fog = new THREE.Fog('#F0E5D4', 1, 100)
        // this.scene.fog = fog

        if(this.debug.active)
        {
            this.debugFolder = this.debug.ui.addFolder('composer')
            this.debugFolder.add(this,'composerEnable')
            // this.debugFolder.add(this.scene.fog,"near",0,200,0.1)
            // this.debugFolder.add(this.scene.fog,"far",0,200,0.1)

        }

        // Resize event
        this.sizes.on('resize', () => {
            this.resize()
        })

        // Time tick event
        this.time.on('tick', () => {
            this.update()
        })
    }

    resize() {
        this.camera.resize()
        this.renderer.resize()
    }

    update() {
        this.camera.update()
        this.world.update()
        if(this.composerEnable){
            this.composer.update()
        }
        else{
            this.renderer.update()

        }
    }

    destroy() {
        this.sizes.off('resize')
        this.time.off('tick')

        // Traverse the whole scene
        this.scene.traverse((child) => {
            // Test if it's a mesh
            if (child instanceof THREE.Mesh) {
                child.geometry.dispose()

                // Loop through the material properties
                for (const key in child.material) {
                    const value = child.material[key]

                    // Test if there is a dispose function
                    if (value && typeof value.dispose === 'function') {
                        value.dispose()
                    }
                }
            }
        })

        this.camera.controls.dispose()
        this.renderer.instance.dispose()

        if (this.debug.active)
            this.debug.ui.destroy()
    }
}